import { Component, OnInit, ViewChild } from '@angular/core';
import { SearchService } from '@modules/search/shared';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { BsModalService } from 'ngx-bootstrap/modal';
import { Library, AuthService, VPreviewInvService, Helper, SocketService, VStringService, sEnum } from '@core/index';
import { DxDataGridComponent } from 'devextreme-angular';
import { VatService } from '@modules/vat/shared';
import { TranslateService } from '@ngx-translate/core';
import { ActivatedRoute, Router } from '@angular/router';
import { SendDataForTaxComponent } from '@shared/index';
import { ViewAttachFileComponent } from '@shared/modules/vat-shared/components/view-attach-file/view-attach-file.component';
import { Subscription } from 'rxjs/Subscription';
import { NgxSpinnerService } from 'ngx-spinner';
import { ApiService, CoreService } from '@core/index';
import { ViewInvoiceService } from '@shared/service/view-invoice.service';
import { InvoiceService } from '@shared/service/invoice.service';
@Component({
  selector: 'app-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.scss']
})
export class IndexComponent implements OnInit {
  @ViewChild(DxDataGridComponent) dataGrid: DxDataGridComponent;
  dataSource: any = {};
  noDataText = 'Không có hóa đơn nào';
  bsModalRef: BsModalRef;
  statusFilter: any;
  _find: any = {};
  _translate: any;
  _language: string;
  extras = [];
  dataInfor: any;
  paramSearch: any;
  checkSubmit: any;
  objExport: any;
  pageSelect: any;
  configs: any = {};
  bsModalSignRef: BsModalRef;
  selectedItems: Array<any> = [];
  selectedRows: Array<any> = [];
  infoDangKy: any;
  isTT78: boolean = false;
  currentUser: any;
  isShowBangTH: boolean = false;
  isSearchInvoice: boolean = true;
  objStatusBtn = {}
  constructor(
    private authService: AuthService,
    private searchService: SearchService,
    private modalService: BsModalService,
    private vatService: VatService,
    private route: ActivatedRoute,
    public translate: TranslateService,
    private router: Router,
    private socketService: SocketService,
    private spinner: NgxSpinnerService,
    private apiService: ApiService,
    private vPreviewInvService: ViewInvoiceService,
    private invoiceService: InvoiceService,
  ) {
    this._translate = this.authService.getTranslate();
    this._language = this.authService.getLanguage();
    translate.use(this._language);
    this.statusFilter = [{
      text: 'HĐ gốc',
      value: '1'
    }, {
      text: 'HĐ bị thay thế',
      value: '2'
    }, {
      text: 'HĐ thay thế',
      value: '3'
    }, {
      text: 'HĐ bị điều chỉnh',
      value: '4'
    }, {
      text: 'HĐ điều chỉnh',
      value: '5'
    }, {
      text: 'HĐ bị xóa bỏ',
      value: '6'
    }, {
      text: 'HĐ xóa bỏ',
      value: '7'
    }, {
      text: 'HĐ điều chỉnh chiết khấu',
      value: '9'
    }];
    this.extras = [{
      id: '1',
      name: this._translate.CONTROLL.BUTTON.export_excel,
      items: [
        {
          id: 'HOA_DON',
          name: "Hóa đơn",
          icon: this._translate.ICONS.excel
        },
        {
          id: 'PXK_KVCNB',
          name: "PXK kiêm VCNB",
          icon: this._translate.ICONS.excel
        },
        {
          id: 'PXK_HGDL',
          name: "PXK gửi đại lý",
          icon: this._translate.ICONS.excel
        }
      ]
    }];
  }

  onRowPrepared(e) {
    if (e.rowType === 'data') {
      switch (e.data.status) {
        case 'DA_XUAT':
          break;
        case 'GHI_TAM':
          if (e.data.invoice_number) {
            if (e.data.send_department_status == "DA_GUI_CQT")
              e.rowElement.style.color = '#008B8B';
            else if (e.data.send_department_status == "DA_CAP")
              e.rowElement.style.color = 'black';
            else if (e.data.send_department_status == "DA_TIEP_NHAN")
              e.rowElement.style.color = 'black';
            else
              e.rowElement.style.color = 'red';
          } else {
            e.rowElement.style.color = '#DEA960';
          }
          break;
        case 'GIU_SO':
          e.rowElement.style.color = '#008B8B';
          break;
        default:
          break;
      }
      if (e.data.adjustment_type === '2' || e.data.adjustment_type === '7') {
        const eles = e.rowElement.getElementsByTagName('td');
        for (let i = 0; i < eles.length; i++) {
          if (i !== 9 && i !== 1) {
            eles[i].style.textDecoration = 'line-through';
          }
        }
      }
    }
  }

  // isFilter = true;
  ngOnInit() {
    this._find = this.route.snapshot.queryParams;
    this.configs = this.authService.getConfigs();
    if (this.configs.system_parameter.select_invoice_current_page)
      this.pageSelect = "page";
    else
      this.pageSelect = "allPages";
    this.currentUser = this.authService.getCurrentUser();
    if (this.currentUser.is_convert_TT78) {
      this.infoDangKy = this.authService.getInfoRegister();
      if (this.infoDangKy && this.infoDangKy.id) {
        if (this.infoDangKy.status == "CHAP_NHAN")
          this.isTT78 = true;
      }
    }
  }

  loadList(params?) {
    if (params) {
      this.dataSource.store = this.searchService.getAll(params);
    } else {

      this.dataSource.store = this.searchService.getAll();
    }
  }
  setUrlNavigate() {
    const url = window.location.pathname;
    if (sessionStorage.hasOwnProperty('urlIndex')) {
      if (sessionStorage.getItem('urlIndex') !== null) {
        sessionStorage.setItem('urlIndex', url);
      } else {
        sessionStorage.setItem('urlIndex', url);
      }
    } else {
      sessionStorage.setItem('urlIndex', url);
    }
  }
  viewNotSelect(data) {
    this.dataInfor = data;
    let ids = [];
    if (data) {
      if (data.xml_type == 4 && VStringService._getValueToString(data.link_pdf_file) != "") {
        let oData =
        {
          url: data.link_pdf_file,
          type: 'pdf'
        }
        let popup = window.open('about:blank', 'name', `width=200, height=100`);
        popup.document.body.innerHTML = this._translate.NOTIFY.loading;
        popup.blur();
        this.spinner.show();
        const sub = this.searchService.getFilePdf(oData).subscribe(rs => {
          if (rs.status === 'success') {
            let w = 1920, h = 900;
            w = window.screen.availWidth * 100 / 100;
            h = window.screen.availHeight * 93 / 100;
            const pdf_file = rs.data.file_content;
            const objectUrl = VPreviewInvService._getUrlInvoice(pdf_file);
            const win = window.open(objectUrl, 'name', 'width=' + w + ', height=' + h);
            win.resizeTo(screen.availWidth, screen.availHeight);
            win.focus();
            this.spinner.hide();
          } else {
            popup.close();
            this.spinner.hide();
            Library.notify(rs.message, 'error');
          }
        }, err => {
          popup.close();
          this.spinner.hide();
        }, () => {
          sub.unsubscribe();
        });
      }
      else {
        ids.push(data.id);
        if (this.configs.system_parameter.view_multi_invoice) {
          let dataView = this.vatService.taodulieuXemMau(ids);
          dataView['configs'] = sEnum.cauhinhxemmau.hoadon;
          this.vPreviewInvService.xemMau(dataView);
        }
        else {
          const initialState = {
            items: ids,
            isXuatHD: false,
            hinhthucxuat: '',
            typeAction: sEnum.typeCode.xem_hoa_don
          };
          this.vPreviewInvService.xemMauPhanTrang(initialState);
        }
      }
    }
    else {
      Library.notify(this._translate.NOTIFY.no_record_selected, 'error');
    }
  }
  getSearch(e) {
    this.paramSearch = e;
    this.dataSource = {};
    this.loadList(e);
  }

  // getAdvandStatusResult(e){
  //   if(e && e[0] == true){
  //     this.isFilter = false;
  //   }else{
  //     this.isFilter = true;
  //   }
  // }
  inChuyenDoi(data) {
    if (data) {
      const ids = [];
      if (data) {
        if (data.xml_type == 4 && VStringService._getValueToString(data.link_pdf_file) != "") {
          let oData =
          {
            url: data.link_pdf_file,
            type: 'pdf'
          }
          let popup = window.open('about:blank', 'name', `width=200, height=100`);
          popup.document.body.innerHTML = this._translate.NOTIFY.loading;
          popup.blur();
          this.spinner.show();
          const sub = this.searchService.getFilePdf(oData).subscribe(rs => {
            if (rs.status === 'success') {
              let w = 1920, h = 900;
              w = window.screen.availWidth * 100 / 100;
              h = window.screen.availHeight * 93 / 100;
              const pdf_file = rs.data.file_content;
              const objectUrl = VPreviewInvService._getUrlInvoice(pdf_file);
              const win = window.open(objectUrl, 'name', 'width=' + w + ', height=' + h);
              win.resizeTo(screen.availWidth, screen.availHeight);
              win.focus();
              this.spinner.hide();
            } else {
              popup.close();
              this.spinner.hide();
              Library.notify(rs.message, 'error');
            }
          }, err => {
            popup.close();
            this.spinner.hide();
          }, () => {
            sub.unsubscribe();
          });
        }
        else {
          if (data.status === 'DA_XUAT') {
            ids.push(data.id);
          } else {
            Library.notify(this._translate.NOTIFY.not_print_convert, 'error');
            return;
          }
          if (this.configs.system_parameter.view_multi_invoice) {
            const dataView = this.vatService.taodulieuInMau(ids, false);
            dataView['configs'] = sEnum.cauhinhxemmau.hoadon;
            this.vPreviewInvService.xemMau(dataView);
          }
          else {
            const initialState = {
              items: ids,
              isXuatHD: false,
              hinhthucxuat: '',
              typeAction: sEnum.typeCode.in_chuyen_doi_hd
            };
            this.vPreviewInvService.xemMauPhanTrang(initialState);
          }
        }
      }
    } else {
      Library.notify(this._translate.NOTIFY.no_record_selected, 'error');
    }
  }

  viewAttachFile(data) {
    if (data) {
      Library.showloading();
      this.vatService.showAttachFile(data.id).subscribe((rs: any) => {
        if (rs.status === 'success') {
          if (rs.data && rs.data.invoice && rs.data.invoice.invoice_files.length > 0) {
            const initialState = {
              fileData: rs.data.invoice.invoice_files,
              view_attach_file: true,
            };
            this.bsModalRef = this.modalService.show(ViewAttachFileComponent, {
              backdrop: 'static',
              keyboard: false,
              class: 'modal-lg',
              initialState
            });

          } else {
            Library.notify(this._translate.NOTIFY.not_file_attach, 'error');
          }

        } else {
          Library.notify(this._translate.NOTIFY.an_error_occurred, 'error');
        }

      }, err => {
      }, () => {
        Library.hideloading();
      });
    }
  }

  customizeText = (url) => {
    this.invoiceService.handleTotalGrid(url.value, this.objStatusBtn, this.dataGrid);
    return '';
  }

  itemExtrasClick(e) {
    if (e.itemData.id === 'HOA_DON') {
      this.xuatExcelHoaDon();
    } else if (e.itemData.id === 'PXK_KVCNB') {
      this.xuatExcelPXKVCNB();
    } else if (e.itemData.id === 'PXK_HGDL') {
      this.xuatExcelPXKHGDL();
    }
  }
  xuatExcelHoaDon() {
    Library.showloading();
    let oSort = this.apiService.oSort;
    if (oSort && oSort.nameColumn == 'invoice_date') {
      let direction = oSort.isDest ? 'DESC' : 'ASC';
      this.objExport.invoices.sort.forEach(item => {
        if (item.value == "invoice_date")
          item.direction = direction;
      });
    }
    this.objExport.invoicetype.loaihoadon = "HOA_DON";
    this.searchService.exportExcel(this.objExport).subscribe(res => {
      if (res.status === 'success') {
        const file = res.data.invoice;
        Helper.dowloadFile(file.file_name, 'xlsx', file.file_content);
        Library.notify('Xuất file Excel thành công', 'success');
      } else {
        Library.notify(res.message, 'error');
      }
      Library.hideloading();
    });
  }
  xuatExcelPXKHGDL() {
    Library.showloading();
    this.objExport.invoicetype.loaihoadon = "PXK_HGDL";
    this.searchService.exportExcel(this.objExport).subscribe(res => {
      if (res.status === 'success') {
        const file = res.data.invoice;
        Helper.dowloadFile(file.file_name, 'xlsx', file.file_content);
        Library.notify('Xuất file Excel thành công', 'success');
      } else {
        Library.notify(res.message, 'error');
      }
      Library.hideloading();
    });
  }
  xuatExcelPXKVCNB() {
    Library.showloading();
    this.objExport.invoicetype.loaihoadon = "PXK_KVCNB";
    this.searchService.exportExcel(this.objExport).subscribe(res => {
      if (res.status === 'success') {
        const file = res.data.invoice;
        Helper.dowloadFile(file.file_name, 'xlsx', file.file_content);
        Library.notify('Xuất file Excel thành công', 'success');
      } else {
        Library.notify(res.message, 'error');
      }
      Library.hideloading();
    });
  }
  convertParamsToObject(e) {
    this.objExport = {
      invoicetype: {
        loaihoadon: null
      },
      invoices:
      {
        student_name: null,
        search_string: null,
        approve_status: null,
        template_code: null,
        invoice_series: null,
        product_name: null,
        status: null,
        user_id: null,
        adjustment_type: null,
        customer_object_code: null,
        send_email_status: null,
        is_buyer_signed: null,
        customer_name: null,
        from_date: null,
        to_date: null,
        other_id: null,
        based_on: null,
        sort:
          [
            {
              value: "invoice_date",
              direction: "DESC"
            },
            {
              value: "invoice_number",
              direction: "ASC"
            },
          ]
      }
    };
    this.objExport.invoices.based_on = e.based_on;
    this.objExport.invoices.search_string = e.contentSearch;
    this.objExport.invoices.approve_status = e.approve_status;
    this.objExport.invoices.template_code = e.template_code;
    this.objExport.invoices.invoice_series = e.invoice_series;
    this.objExport.invoices.product_name = e.product_name;
    this.objExport.invoices.status = e.status;
    this.objExport.invoices.user_id = e.user_id;
    this.objExport.invoices.adjustment_type = e.adjustment_type;
    this.objExport.invoices.customer_object_code = e.customer_object_code;
    this.objExport.invoices.send_email_status = e.send_email_status;
    this.objExport.invoices.is_buyer_signed = e.is_buyer_signed;
    this.objExport.invoices.customer_name = e.customer_name;
    this.objExport.invoices.from_date = e.startDate;
    this.objExport.invoices.to_date = e.endDate;
    this.objExport.invoices.student_name = e.student_name;

    this.objExport.invoices.sort.forEach((item, index) => {
      if (item.value == "invoice_number" || VStringService._getValueToString(item.value) == '') {
        item.value = "invoice_number";
        item.direction = e.sortinvoicenumber ? e.sortinvoicenumber : "ASC";
      }
    });
    if (Array.isArray(e.other_id)) {
      if (e.other_id.length == 1) {
        if (e.other_id[0] == "TU_XUAT") {
          this.objExport.invoices.other_id = 'in_app';
        }
        else if (e.other_id[0] == "TICH_HOP") {
          this.objExport.invoices.other_id = 'out_app';
        }
      }
    }
  }
  GuiBangDuLieu() {
    const initialState = {
      invoices: this.selectedItems,
      configs: this.configs,
      notifyType: "GUI_BANG_TONG_HOP",
      infoDangKy: this.infoDangKy
    };
    this.bsModalSignRef = this.modalService.show(SendDataForTaxComponent, {
      backdrop: 'static',
      keyboard: true,
      class: 'sendTax modal-lg modal-margin-width',
      initialState
    });
    const subModal = this.modalService.onHide.subscribe(() => {
      if (this.bsModalSignRef.content.successSaveData) {

      }
      subModal.unsubscribe();
    });
  }
  // Chọn đối tượng trong màn hình danh sách
  selectionChanged(data: any) {
    this.selectedItems = data.selectedRowsData;
  }
  ngOnDestroy() {
  }
  /* Trạng thái thanh toán */
  trangthaithanhtoan(data) {
    if (Number.parseInt(data.invoice_number) > 0) {
      if (data.payment_status)
        return "Đã thanh toán";
      else
        return "Chưa thanh toán";
    }
    else
      return "";
  }
  LydoKTiepNhan(data) {
    let lstErr = JSON.parse(data.error_content_invoice);
    const initialState = {
      lstErr: lstErr,
      notifyType: "THONG_BAO_LOI_CQT"
    };
    this.bsModalSignRef = this.modalService.show(SendDataForTaxComponent, {
      backdrop: 'static',
      keyboard: true,
      class: 'modal-lg modal-margin-width',
      initialState
    });
    const subModal = this.modalService.onHide.subscribe(() => {
      subModal.unsubscribe();
    });
  }
  calculateFilterExpression(value, selectedFilterOperations, target) {
    const column = this as any;
    if (target === 'headerFilter' && value === 'weekends') {
      return "";
    }
    return column.defaultCalculateFilterExpression.apply(this, arguments);
  }
}
