import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { IndexComponent } from './components/index/index.component';
import { DxTreeListModule } from 'devextreme-angular';
//library
import { devextremeModule } from '@core/index';
import { SendDataForTaxComponent, ViewInvoiceMultipageComponent } from '@shared/index';

//general
import { SharedModule,VatSharedModule } from '@shared/index';
import { routing } from './search.routing';
import { VatService } from '@modules/vat/shared';
import { NgxSpinnerModule } from 'ngx-spinner';
import { ViewAttachFileComponent } from '@shared/modules/vat-shared/components/view-attach-file/view-attach-file.component';

@NgModule({
  imports: [
    CommonModule,
    routing,
    SharedModule,
    VatSharedModule,
    devextremeModule,
    NgxSpinnerModule
  ],
  declarations: [IndexComponent],
  entryComponents: [
    ViewAttachFileComponent,
    SendDataForTaxComponent,
    ViewInvoiceMultipageComponent],
  providers: [VatService]
})
export class SearchModule { }
